<template>
    <section ref="scroll">
        <slot></slot>
    </section>
</template>

<script>
    import BScroll from 'better-scroll'
    export default {
        name: "scroll",
        props:{
            probeType: { //  probeType  1 scroll 
                type: Number,
                default: 1
            },
            click: { // better-scroll  click  truebetter-scroll  click 
                type: Boolean,
                default: true
            },
            scrollX:{
                type:Boolean,
                default:true
            },
            scrollY:{
                type:Boolean,
                default:true
            },
            // 
            listenScroll: {
                type: Boolean,
                default: false
            },
            mouseWheel: {
                type:Boolean,
                default:false
            },
            data: { // better-scroll
                type: Array,
                default() {
                    return null
                }
            }
        },
        watch:{
            data(){
                setTimeout(()=>{
                    this.refresh()
                },20)
            }
        },
        mounted(){
            setTimeout(()=>{
                this._initScroll()
            },20) // 14msDOM

            window.addEventListener('resize',()=>{
                if (!this.scroll){
                    return
                }
                this.refresh()
            })
        },
        methods:{
            _initScroll(){
                if (!this.$refs.scroll) {
                    return
                }
                this.scroll = new BScroll(this.$refs.scroll,{
                    probeType: this.probeType,
                    click: this.click,
                    scrollX: this.scrollX, // 
                    scrollY: this.scrollY,
                    mouseWheel: this.mouseWheel
                })

                let me = this
                if (this.listenScroll) {
                    this.scroll.on('scroll',(pos)=>{
                        me.$emit('scroll',pos)
                    })

                    this.scroll.on('scrollEnd',(pos)=>{
                        me.$emit('scrollEnd',pos)
                    })
                }
            },
            refresh(){
                this.scroll && this.scroll.refresh()
            },
            scrollTo(){
                this.scroll&&this.scrollTo().apply(this.scroll,arguments)
            }
        }
    }
</script>



